.modal__submit-button_disabled{
  border: 1px solid rgba(0, 0, 0, .2);
  box-sizing: border-box;
  border-radius: 2px;
  color: rgba(0, 0, 0, .2);
  background-color: #fff;
}

.modal__submit-button_disabled:hover{
  text-decoration: none;
  color: rgba(0, 0, 0, .2);
  background-color: #fff;
  cursor: auto;
}